
.loseout-enter-active,
.loseout-leave-active {
  transition: all 0.5s;
}
.loseout-enter,
.loseout-leave-active {
  opacity: 0;
}
.rightin-enter-active,
.rightin-leave-active {
  transition: all 0.5s;
}
.rightin-enter,
.rightin-leave-active {
  transform: translate3d(100%, 0, 0);
}
