
html,
body {
  font-family: '微软雅黑', 'PingFang SC', 'STHeightSC-Light', 'Helvetical-Leight', 'arial', 'sans-serif';
}
#app .tab {
  width: 100%;
  height: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row;
      flex-flow: row;
  position: relative;
}
#app .tab::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
  border-top: 1px solid rgba(7,17,27,0.1);
}
#app .tab .tab-item {
  -ms-flex: 1;
      flex: 1;
  line-height: 40px;
  text-align: center;
}
#app .tab .tab-item > .router-link {
  display: inline-block;
  width: 100%;
  height: 100%;
  font-size: 14px;
  color: #4d555d;
}
#app .tab .tab-item > .router-link.router-link-active {
  color: #f01414;
}
#app .fade-enter,
#app .fade-leave-active {
  opacity: 0;
}
#app .fade-enter-active,
#app .fade-leave-active {
  transition: all 0.5s;
  position: absolute;
}


