
html,
body {
  font-family: '微软雅黑', 'PingFang SC', 'STHeightSC-Light', 'Helvetical-Leight', 'arial', 'sans-serif';
}
.ratingselect .rating-detail .rating-type {
  padding: 12px 0 18px 0;
  position: relative;
}
.ratingselect .rating-detail .rating-type::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.ratingselect .rating-detail .rating-type .type {
  display: inline-block;
  margin-right: 8px;
  padding: 8px 12px;
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  background-color: rgba(0,160,220,0.2);
  color: #4d555d;
}
.ratingselect .rating-detail .rating-type .type.selected {
  background-color: #00a0dc;
  color: #fff;
}
.ratingselect .rating-detail .rating-type .type:last-child {
  background-color: rgba(77,85,93,0.2);
  margin-right: 0;
  color: #4d555d;
}
.ratingselect .rating-detail .rating-type .type:last-child.selected {
  background-color: #4d555d;
  color: #fff;
}
.ratingselect .rating-detail .rating-type .type .num {
  font-size: 8px;
}
.ratingselect .rating-detail .only-content {
  width: 100%;
  padding: 12px 0 18px 0;
  position: relative;
}
.ratingselect .rating-detail .only-content::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.ratingselect .rating-detail .only-content.hasContent .icon-anonymous-iconfont {
  color: #00c850;
}
.ratingselect .rating-detail .only-content.hasContent .content {
  font-weight: 700;
}
.ratingselect .rating-detail .only-content .icon-anonymous-iconfont {
  font-size: 24px;
  line-height: 24px;
  color: #93999f;
}
.ratingselect .rating-detail .only-content .content {
  font-size: 12px;
  color: #93999f;
  line-height: 24px;
  vertical-align: top;
}
.ratingselect .rating-detail .ratings-list .list .rating-item {
  positive: relative;
  padding: 16px 0;
  position: relative;
}
.ratingselect .rating-detail .ratings-list .list .rating-item::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.ratingselect .rating-detail .ratings-list .list .rating-item time {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .user {
  float: right;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .user .username {
  font-size: 10px;
  color: #93999f;
  line-height: 12px;
  padding-right: 6px;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .user img {
  border-radius: 50%;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .content {
  padding-top: 6px;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .content .icon-cai,
.ratingselect .rating-detail .ratings-list .list .rating-item .content icon-dianzan {
  font-size: 12px;
  line-height: 24px;
  color: #93999f;
  padding-right: 4px;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .content .icon-dianzan {
  color: #00a0dc;
}
.ratingselect .rating-detail .ratings-list .list .rating-item .content .text {
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
  padding-right: 4px;
}


