
html,
body {
  font-family: '微软雅黑', 'PingFang SC', 'STHeightSC-Light', 'Helvetical-Leight', 'arial', 'sans-serif';
}
.fadein-enter-active,
.fadein-leave-active {
  transition: all 0.5s;
}
.fadein-enter,
.fadein-leave-active {
  opacity: 0;
}
.flod-enter-active,
.flod-leave-active {
  transition: all 0.5s;
}
.flod-enter,
.flod-leave-active {
  transform-origin: 0% 200%;
  transform: rotateX(90deg);
}
.drop-enter-active,
.drop-leave-active {
  transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
