
.slod-enter-active,
.slod-leave-active {
  transition: all 0.5s;
}
.slod-enter,
.slod-leave-active {
  opacity: 0;
  transform: translate3d(24px, 0, 0) rotate(360deg);
}
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-decrease .icon-jian {
  color: #00a0dc;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
}
.cartcontrol .cart-count {
  display: inline-block;
  width: 24px;
  line-height: 24px;
  vertical-align: top;
  margin-top: 6px;
  font-size: 10px;
  color: #93999f;
  text-align: center;
}
.cartcontrol .cart-increase {
  display: inline-block;
  padding: 6px;
}
.cartcontrol .cart-increase .icon-anonymous-iconfont {
  color: #00a0dc;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
}


